<template>
  <div class="diary">
    <div class="diaryAL" >
        <div class="diaryCont" v-for="(itme,index) in diary.slice(0, 4)" :key="index.id" >
            <div class="boli" ref="backimg" :style="{ 'background-image': 'url(' + bimg[index] + ')','background-repeat':'no-repeat','background-size':'cover'}"></div>
            <div class="diaryTE">
                <div class="diaryTitle"><h2>{{itme.title}}</h2></div>
                <div class="diaryText" @click="link(itme)">{{itme.text}}</div>
                <div class="diaryTime">{{itme.time}}</div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import {getHomeMultiData} from '../network/backgroundImge'
export default {
    
    name:"diary",
    data(){
        return {
            diary:[{id:1,title:"闲来无事1",text:"正在测试1",time:"2021.5.18"},
                    {id:1,title:"闲来无事2",text:"正在测试2",time:"2021.5.18"},
                    {id:1,title:"闲来无事3",text:"正在测试3",time:"2021.5.18"}],
            baImg:"../assets/headPortrait/back.jpg",
            bimg:[],
            note:{
                backgroundImage:"0",
                backgroundRepeat: "no-repeat",
                backgroundSize: "cover",
            },
        }        
    },
    created() {
        this.backgroundImge()
        
    },
    methods: {
        link(e){
            this.$router.push('/about/'+ e.id);
            // console.log('tag', e.id)
        },
        backgroundImge(){
            for(let i =0 ;i<4;i++){
                getHomeMultiData().then(res =>{
                    let imge = res;
                    this.bimg.push("'"+imge.data.imgurl+"'");
                })
            }
        }
    },
    mounted(){
  
    }

}
</script>

<style>
.diaryAL{
    width: 100%;
    background-color: rgba(0, 0, 0,0.2);
    margin:20px 0;
    padding:10px 0;
    border-radius: 5px;
}
.diaryCont{
    width:92%;
    height: 270px;
    margin:20px auto;
    border-radius: 10px;
    position: relative;
    background-color:rgba(0, 0, 0,0.1);
    overflow: hidden;
}
.boli{
    position: absolute;
    border-radius: 10px;
    width:100%;
    height: 270px;
    z-index: -1;
}

.diaryTE{
    width:100%;
    height: 270px;
    margin:15px auto;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    overflow: hidden;
    cursor: pointer;
}
.diaryCont>div>div{
    padding: 10px;
    text-align: center;
    color: white;
    transition: all 0.3s;
}
.diaryTitle{
    font-size: 25px;
    margin-top: 120px;
}
.diaryText{
    height: 35%;
    cursor: pointer;
    margin-top: 50px;
    opacity: 0;
}
.diaryTime{
    opacity: 0;
    margin-top: 10px;
}

.diaryCont:hover .boli{
    position: absolute;
    filter:blur(4px);
}
.diaryTE:hover{
    width:100%;
    height: 270px;
    background-color: rgba(255, 255, 255,0);
    margin:15px auto;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    overflow: hidden;
}
.diaryTE:hover .diaryCont>div>div{
    padding: 10px;
    text-align: center;
    color: white;
}
.diaryTE:hover .diaryTitle{
    margin-top: 40px;
    font-size: 20px;
}
.diaryTE:hover .diaryText{
    height: 35%;
    margin-top: -50px;
    
    opacity: 1;
}
.diaryTE:hover .diaryTime{
    opacity: 1;
    margin-top: -100px;
}

</style>